.create-page {
  width: 100vw;
  height: auto;
}

.creatures-return-link {
  font-size: 1.5rem;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  background: linear-gradient(-20deg,
  rgba(77, 126, 155, 0.9),
  rgba(124, 161, 182, 0.8));
  box-shadow: 0 0 3px black;
  color: white;
  width: 15rem;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  left: 10rem;
  top: 10rem;
  transition: transform 0.2s;
}

.creatures-return-link:hover {
  transform: scale(1.1);
}

.creature-form-container {
  margin: 0 auto;
  text-align: center;
}

.max-creatures-msg,
.creature-form {
  background: linear-gradient(-45deg,
  #315659,
  #496a6d,
  #608183);
  box-shadow: 0 2px 6px black;
  color: rgb(231, 231, 231);
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 60rem;
  height: auto;
  margin: 2rem auto;
  text-align: center;
  border-radius: 1rem;
  font-family: 'Open Sans', sans-serif;
}

.form-title {
  font-size: 2.5rem;
  color: white;
  display: block;
  margin: 0 auto 4rem auto;
}

.creature-form form {
  display: block;
  width: auto;
  height: auto;
  margin: 5rem auto 0 auto;
  font-size: 1.3rem;
}

.creature-form form label {
  margin: 0.5rem;
}

.creature-form form input {
  font-size: 1.2rem;
}

.creature-form form select {
  font-size: 1.2rem;
  width: auto;
}

#input-parent-container {
  display: block;
  margin: 0 auto 3rem auto;
  width: auto;
  height: auto;
  text-align: center;
  columns: 2;
}

#evo-input-parent-container {
  display: block;
}

#evo-input-header {
  text-align: right;
  margin: 0 auto 1rem auto;
  display: block;
}

#evo-info {}

#evo-input-container {}

.evo-input-child-container {
  display: block;
  margin: 0 auto 0.25rem auto;
  columns: 2;
}

.creature-info-container {}

.evo-description {
  display: block;
  text-align: right;
  margin: 0 auto;
}

.creature-select {}

.select-creature-input {
  display: block;
}

#mega-input-parent-container {}

#mega-input-header {
  display: block;
  margin: 0 auto 1rem auto;
  text-align: center;
}

#mega-info {}

#mega-input-container {}

.mega-input-child-container {
  display: block;
  margin: 0 auto;
}

.input-mega-div {}

.mega-input {
  width: 15rem;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  margin: 0 auto;
}

.info {
  font-size: 0.75em;
  margin: 0.2rem 0.8rem;
  display: block;
}

.select-purpose {}

.creature-label {
  width: 10rem;
  height: auto;
}

.purpose-label {
  text-align: center;
  margin: 0 auto;
  margin-bottom: 1rem;
}

.purpose-select {
  margin: 0 auto;
  margin-bottom: 3.5rem;
  font-size: 1.2rem;
}

#name-container {
  display: block;
  margin: 3rem auto;
}

#name-label {
  display: block;
  margin: 1rem auto;
}

#name-input {
  display: block;
  margin: 0 auto;
}

#title-container {
  display: block;
  margin: 2rem auto;
}

#title-label {
  display: block;
  margin: 1rem auto;
}

#title-input {
  display: block;
  margin: 0 auto;
}

.submit-form-btn {
  margin: 1rem auto 4rem auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(251,202,99);
  color: black;
  font-weight: 600;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  width: 13rem;
  height: 5rem;
  border: none;
  cursor: pointer;
  transition: transform 0.2s;
}

.submit-form-btn:hover {
  transform: scale(1.1);
}

@media only screen and (min-device-width: 120rem) {
  .creature-form {
    white-space: nowrap;
  }
}

